import React from 'react';

const MapComponent = () => {

const htmlFilePath = 'http://yourserver.com/춘천중앙시장.html'
  return (
    <div style={{ height: '400px', width: '100%' }}>
      <iframe
        title="map"
        src={htmlFilePath}
        width="100%"
        height="100%"
        frameBorder="0"
        allowFullScreen=""
        aria-hidden="false"
        tabIndex="0"
      ></iframe>
    </div>
  );
};

export default MapComponent;
